import React, { Component } from "react";

class Card extends Component {
    render() {
        const hasLink = this.props.project.link != "";
        const cardClass = this.props.project.size + " card";

        const content = <div className={hasLink ? "" : "no-link"} title={hasLink ? "" : "No link available"}>
                            <div className="card-overlay"></div>
                            <h2 className="card-header text-header scale-text-body">{  this.props.project.header  }</h2>
                        </div>;

        const wrapper = hasLink ? <a href={this.props.project.link} target="_blank" rel="noreferrer"> { content } </a> : content;

        return (
            <div className={cardClass}>
                <div className="card-bg" style={{backgroundImage: `url(${this.props.project.img})`}}>
                    {/* <a href={this.props.project.link} target="_blank">
                        <div className="card-overlay"></div>
                        <h2 className="card-header text-header scale-text-body">{  this.props.project.header  }</h2>
                    </a> */}
                    { wrapper }
                </div>
            </div>
        )
    }
}

export default Card;